@import url(https://fonts.googleapis.com/css?family=Asap+Condensed&display=swap);
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Asap Condensed', -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-width: 320px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.field {
  background: black;
  width: 150px;
  height: 250px;
  flex: 0 0 auto;
  margin-right: 1rem;
  -webkit-transition: width 2s, height 2s;
  transition: width 2s, height 2s;
}

@media (min-width: 450px) {
  .field {
    width: 225px;
    height: 375px;
  }
}

@media (min-width: 600px) {
  .field {
    width: 300px;
    height: 500px;
  }
}

.info {
  background: #898996;
  flex: 0 0 auto;
  height: 250px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 0.8rem;
  padding: 1rem;
  -webkit-transition: font-size 2s, padding 2s, height 2s;
  transition: font-size 2s, padding 2s, height 2s;
}

.info button {
  font-size: 0.7rem;
  font-family: 'Asap Condensed', sans-serif;
  cursor: pointer;
  margin: 1rem;
}

.info span {
  margin: 1rem;
}

.info span b {
  padding-right: 0.5rem;
}

@media (min-width: 450px) {
  .info {
    font-size: 1rem;
    padding: 1.5rem;
    height: 375px;
  }
  .info button {
    font-size: 0.9rem;
  }
}

@media (min-width: 600px) {
  .info {
    padding: 2rem;
    height: 500px;
  }
}

.app {
  text-align: center;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2rem;
}

